import React from "react";
import ProductItem from "./productItem";
import { withGoogleSheets } from "react-db-google-sheets";

const sheet = "Hoja 1";

const ProductList = ({ db, categoryId }) => {
  const parsedCat = categoryId.replaceAll("-", " ");
  const list = db[sheet]
    .filter((elem) => elem.category === categoryId)
    .map((elem) => <ProductItem key={elem.ref} product={elem} />);
  return (
    <div className="product-list-container">
      <h1>Listado de productos de {parsedCat}</h1>
      <ul className="product-list">{list}</ul>
    </div>
  );
};

export default withGoogleSheets(sheet)(ProductList);
