import { Link } from "@reach/router";
import React from "react";
import ProductColors from "./productColors";

const ProductItem = ({ product }) => {
  const url = `https://www.velillaconfeccion.com/media/catalog/product/cache/1/small_image/150x185/9df78eab33525d08d6e5fb8d27136e95/P${product?.ref}/default.jpg`;
  const getSizes = () =>
    product?.tallas
      .trim()
      .split("/")
      .map((e) => e.trim())
      .join(" / ");

  return (
    <div>
      <Link to={`/producto/${product?.ref}`} className="product-item">
        <img
          className="product-item__image"
          src={url}
          alt={product?.name}
          onError="this.onerror=null;this.src='https://www.velillaconfeccion.com/media/catalog/product/cache/1/image/280x440/9df78eab33525d08d6e5fb8d27136e95/placeholder/default/noimage.png'"
        />
        <span className="product-item__ref">Ref. {product?.ref}</span>
        <span className="product-item__name">
          {product?.name.charAt(0).toUpperCase() + product?.name.slice(1)}
        </span>
        <ProductColors colors={product?.colors} small />
        <span className="product-item__sizes">{getSizes()}</span>
      </Link>
    </div>
  );
};

export default ProductItem;
