import { Link } from "@reach/router";
import React, { useEffect, useState } from "react";
import ProductColors from "./productColors";
import { getColorName } from "../utils";
const ProductDetail = ({ product }) => {
  const [selectedColor, setSelectedColor] = useState(null);
  const [category, setCategory] = useState("");

  const baseImgUrl = `https://www.velillaconfeccion.com/media/catalog/product/cache/1/image/280x440/9df78eab33525d08d6e5fb8d27136e95/P${product?.ref}`;

  useEffect(() => {
    if (product) {
      const categoryLine = product.category.replaceAll("-", " ");

      setCategory(categoryLine);
      setSelectedColor(product?.colors.split("/")[0]);
    }
  }, [product]);

  const handleClick = (e) => {
    const color = !!e.currentTarget.value ? e.currentTarget.value : 0;
    setSelectedColor(color);
  };

  const getImg = () =>
    product?.colors
      .split("/")
      .filter((item) => item === selectedColor)
      .map((elem) => (
        <img
          data-image={`color-${elem}`}
          key={`color-${elem}`}
          className="active"
          src={`${baseImgUrl}/frontal_${elem.replaceAll("-", "_")}.jpg`}
          alt=""
        />
      ));
  const getSizes = () =>
    product?.tallas
      .trim()
      .split("/")
      .map((e) => e.trim())
      .join(" / ");

  return product ? (
    <main className="container">
      <div className="left-column">{getImg()}</div>

      <div className="right-column">
        <div className="product-description">
          <span>{category}</span>
          <p className="product-ref">Ref. {product?.ref}</p>
          <h1>
            {product?.name.charAt(0).toUpperCase() + product?.name.slice(1)}
          </h1>
        </div>

        <div className="product-configuration">
          <div className="product-feature">
            <span className="product-feature__title">Colores disponibles</span>
            <ProductColors colors={product?.colors} handleClick={handleClick} />
            <span className="product-feature__color-name">
              {getColorName(selectedColor)}
            </span>
          </div>
          <div className="product-feature product-size">
            <span className="product-feature__title">
              Tallas elegibles{" "}
              <span className="product-sizes__link">
                (<Link to="/guia-de-tallas">Guía de tallas</Link>)
              </span>
            </span>

            <div className="size-choose">{getSizes()}</div>
          </div>
        </div>
      </div>
    </main>
  ) : (
    <h1>Producto no encontrado</h1>
  );
};

export default ProductDetail;
